import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
//library
import { devextremeModule } from '@core/index';

//general
import { SharedModule } from '@shared/index';
import { ApproveService } from './shared';
import { VatService } from '@modules/vat/shared';
import { routing } from './approve.routing';
import { IndexComponent } from './components/index/index.component';
import { SignManyComponent } from './components/sign-many/sign-many.component';

@NgModule({
  imports: [
    CommonModule,
    routing,
    SharedModule,
    devextremeModule
  ],
  declarations: [
    IndexComponent,
    SignManyComponent,
  ],
  entryComponents: [
    SignManyComponent
  ],
  providers: [ApproveService, VatService]
})
export class ApproveModule { }
