import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';

import {ApproveService} from '@modules/approve/shared';
import {VatService} from '@modules/vat/shared';
import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {BsModalService} from 'ngx-bootstrap/modal';
import {Library, Helper, AuthService, VPreviewInvService, VStringService} from '@core/index';
import {SignManyComponent} from '../sign-many/sign-many.component';
import {TranslateService} from '@ngx-translate/core';
import {VSelectBoxService} from '@core/library/public/v-select-box.service';
import * as moment from 'moment';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {

  dataSource: any;
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  bsModalRef: BsModalRef;
  bsModalImportRef: BsModalRef;
  bsModalSignRef: BsModalRef;
  listDM: any;
  showKeepNumber: boolean = false;
  invoice_releases = [];
  paramSearch: any;
  objParams: any = {};
  _translate: any;
  approveStatus = [];
  checkSubmit: any;
  loaiHoaDon: any;
  //dropbutton
  extras = [{
    id: '1',
    name: 'Mở rộng',
    items: [{
      id: 'NAP_HD_TU_EXCEL',
      name: 'Nạp HĐ từ Excel',
    }]
  }];
//end-dropbutton

  select: any = {};
  configs: any;
  disEditMulti = true;
  hoaDonImportChoXuat: any;

  exportSchedules: any;
  exportSchedulesDefault;
  startDate: any;
  endDate: any;
  contentSearch: any;

  constructor(
    private approveService: ApproveService,
    private router: Router,
    private authService: AuthService,
    private vatService: VatService,
    private modalService: BsModalService,
    private modalImportService: BsModalService,
    public translate: TranslateService,
    private route: ActivatedRoute,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    this.loaiHoaDon = this.route.snapshot.data.type;
    if (localStorage.getItem('new_member')) {
      Library.notify('Bạn phải tạo thông báo phát hành trước khi lập hóa đơn', 'warning');
    }
    this.exportSchedules = Helper.getExportSchedule(this._translate).schedule;
    this.exportSchedulesDefault = this.exportSchedules[3].code;
    this.startDate = this.exportSchedules[3].startDate;
    this.endDate = this.exportSchedules[3].endDate;
    this.approveStatus = VSelectBoxService._approveStatus(this._translate);
    if (this.approveStatus) {
      this.approveStatus.splice(1, 1);
    }
    this.sendDateChange();
  }

  loadList(params?) {
    this.dataSource = {};
    let ref = {};
    if (params) {
      let _lst = this.vatService.getAll(this.loaiHoaDon, params);
      this.dataSource.store = _lst;
      // this.footerSum = {
      //   summary: _lst.summary,
      //   summary_deduction: _lst.summary_deduction,

      // };
    } else {
      this.dataSource.store = this.vatService.getAll(this.loaiHoaDon);
    }
  }

  // loadList(params?){
  //   this.dataSource = {};
  //   if(params){
  //     this.dataSource.store = this.approveService.getAll(params);
  //   }else{
  //     this.dataSource.store = this.approveService.getAll();
  //   }
  // }

  onRowPrepared(e) {

  }

  getSearch(e) {
    this.paramSearch = e;
    this.loadList(e);
  }

  // Chọn đối tượng trong màn hình danh sách
  selectionChanged(data: any) {
    this.disEditMulti = true;
    this.selectedItems = data.selectedRowsData;
    if (this.selectedItems.length > 1) {
      let checks = this.selectedItems.filter((item) => {
        return item.number_of_vouchers;
      });
      if (checks.length == this.selectedItems.length) {
        this.disEditMulti = false;
      }
    }
  }

  lapHoaDon() {
    this.router.navigate(['system/vat-invoice/create']);
  }

  edit(row) {
    this.kyHangLoat();
  }

  delete() {
    let status = this.selectedItems[0].status;
    if (status == 'GHI_TAM') {
      const ids = this.selectedRows.join(',');
      var result = Library.confirm('Bạn có chắc chắn muốn xóa đối tượng đã chọn?', 'Thông báo');
      result.then((dialogResult) => {
        if (dialogResult) {
          Library.showloading();
          this.approveService.delete(ids).subscribe(rs => {
            if (rs.status === 'success') {
              Library.notify('Xóa dữ liệu thành công', 'success');
              this.loadList(this.paramSearch);
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
        }
      });
    } else {
      Library.notify('Bạn không thể xóa hóa đơn được chọn', 'error');
    }
  }

  onExportSchedules(e) {
    let selectedItem = e.component.option('selectedItem');
    this.startDate = selectedItem.startDate;
    this.endDate = selectedItem.endDate;
    this.sendDateChange();
  }

  onStartDate(e) {
    if (e.event) {
      this.startDate = moment(e.value).format('YYYY-MM-DD HH:mm:ss');
      this.sendDateChange();
    }
  }

  onEndDate(e) {
    if (e.event) {
      this.endDate = moment(e.value).format('YYYY-MM-DD HH:mm:ss');
      this.sendDateChange();
    }
  }

  sendDateChange() {
    let paramsDateSearchs = `filter_groups[999][filters][0][key]=created_at&filter_groups[999][filters][0][value][0]=${this.startDate}&filter_groups[999][filters][0][value][1]=${this.endDate}&filter_groups[999][filters][0][operator]=bt&`;
    let param = paramsDateSearchs.slice(0, -1);
    this.loadList(param);
    this.paramSearch = param;
  }

  search() {
    let paramsDateSearchs = `filter_groups[999][filters][0][key]=created_at&filter_groups[999][filters][0][value][0]=${this.startDate}&filter_groups[999][filters][0][value][1]=${this.endDate}&filter_groups[999][filters][0][operator]=bt&`;
    let paramsContentSearchs = paramsDateSearchs + `filter_groups[1000][or]=true&filter_groups[1000][filters][0][key]=buyer_tax_code&filter_groups[1000][filters][0][value]=${this.contentSearch}&filter_groups[1000][filters][0][operator]=ct&filter_groups[1000][filters][1][key]=customer_name&filter_groups[1000][filters][1][value]=${this.contentSearch}&filter_groups[1000][filters][1][operator]=ct&filter_groups[1000][filters][2][key]=buyer_name&filter_groups[1000][filters][2][value]=${this.contentSearch}&filter_groups[1000][filters][2][operator]=ct`;
    if (this.objParams.approve_status) {
      if (Array.isArray(this.objParams.approve_status)) {
        if (this.objParams.approve_status.length > 0) {
          let _obj = this.objParams.approve_status.filter(p => p == 'TAT_CA')[0];
          if (!_obj) {
            paramsContentSearchs += VStringService._removeNewline(`&filter_groups[1001][filters][3][key]=approve_status`);
            for (let i = 0; i < this.objParams.approve_status.length; i++) {
              paramsContentSearchs += VStringService._removeNewline(`&filter_groups[1001][filters][3][value][${i}]=${this.objParams.approve_status[i]}`);
            }
            paramsContentSearchs += VStringService._removeNewline(`&filter_groups[1001][filters][3][operator]=in`);
          }
        }
      }
    }
    this.loadList(paramsContentSearchs);
    this.paramSearch = paramsContentSearchs;
  }

  onapproveStatusChanged(e) {
    const nodes = e.component.getNodes();
    this.objParams.approve_status = this.getSelectedItemsKeys(nodes);
  }

  getSelectedItemsKeys(items) {
    let result = [],
      that = this;
    items.forEach(function (item) {
      if (item.selected) {
        result.push(item.key);
      }
      if (item.items.length) {
        result = result.concat(that.getSelectedItemsKeys(item.items));
      }

    });
    return result;
  }

  kyHangLoat() {
    if (this.selectedItems.length) {
      let listChoDuyet = this.selectedItems.filter(p => p.approve_status == '1');
      if (listChoDuyet.length == 0) {
        Library.notify('Chưa chọn hóa đơn chờ duyệt', 'error');
        return false;
      }
      Library.showloading();
      this.vatService.layCacHoaDonKySoHangLoat(this.selectedRows, this.loaiHoaDon).subscribe((rs: any) => {
        if (rs[0].status == 'success') {
          rs[0].data.invoices.forEach(element => {
            element.dongy = null;
            element.tuchoi = null;
          });
          let initialState = {
            invoiceList: rs[0].data.invoices,
            configs: rs[1].data.configs,
            invoice_releases: Helper.convertDataToInvoiceResolver(rs[2])
          };
          this.bsModalSignRef = this.modalService.show(SignManyComponent, {
            backdrop: 'static',
            keyboard: false,
            class: 'modal-lg full-modal',
            initialState
          });
          let subModal = this.modalService.onHide.subscribe(() => {
            if (this.bsModalSignRef.content.successSaveItem) {
              this.selectedItems = [];
              this.selectedRows = [];
              this.loadList(this.paramSearch);
              subModal.unsubscribe();
            }
          });
        } else {
          Library.notify('Có lỗi xảy ra', 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    } else {
      Library.notify('Vui lòng chọn hóa đơn', 'error');
    }
  }

  view(data) {
    if (this.selectedItems.length || data) {
      let _lst = this.selectedItems;
      if (_lst.length > 0 || data) {
        let ids = [];
        if (data) {
          ids.push(data.id);
        } else {
          ids = _lst.map(e => e.id);
        }
        let w = 1920, h = 900;
        w = window.screen.availWidth * 100 / 100;
        h = window.screen.availHeight * 93 / 100;
        this.checkSubmit = window.open('about:blank', 'name', 'width=200, height=100');
        this.checkSubmit.document.body.innerHTML = this._translate.NOTIFY.loading;
        this.checkSubmit.blur();
        const timer = setInterval(() => {
          if (this.checkSubmit.closed) {
            clearInterval(timer);
            const isCheck = Helper.getCookie('AK_SS_I');
            this.checkSubmit = undefined;
            if (isCheck === 'CHECKED') {
              document.cookie = 'AK_SS_I=; path=/';
              const status = 'HUY';
              this.router.navigate(['/system/handle-report/', data.id, status]);
            } else if (isCheck === 'CHECKED2') {
              document.cookie = 'AK_SS_I=; path=/';
              const status = 'THAY_THE';
              this.router.navigate(['/system/handle-report/', data.id, status]);
            } else if (isCheck === 'CHECKED3') {
              document.cookie = 'AK_SS_I=; path=/';
              const status = 'DIEU_CHINH';
              this.router.navigate(['/system/handle-report/', data.id, status]);
            } else if (isCheck === 'CHECKED4') {
              document.cookie = 'AK_SS_I=; path=/';
            }
          }
        }, 500);

        Library.showloading();
        const sub = this.vatService.xemHoaDonInvoiceImport(ids).subscribe(rs => {
          if (rs.status === 'success') {
            const pdf_file = rs.data.invoice.pdf_file;
            const objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
            const a = true;
            const b = '<h3>Hello</h3>';
            if (a) {

            }
            const myPara = '<div class="design-button">' +
              '<div style="width: 50%; display: inline-block; float: left">' +
              '<div class="dropdown">' +
              '<button type="button" class="dropbtn button-content-style">' +
              '<a href="javascript:void(0)" class="text-button">Xử lý hóa đơn</a>' +
              '</button>' +
              '<div class="dropdown-content" style="left:0;">' +
              '<a href="javascript:void(0)" onclick="setConfirmInvoice()">Hủy bỏ hóa đơn</a>' +
              '<a href="javascript:void(0)" onclick="setConfirmInvoice2()">Thay thế hóa đơn</a>' +
              '<a href="javascript:void(0)" onclick="setConfirmInvoice3()">Điều chỉnh hóa đơn</a>' +
              '<a href="javascript:void(0)" onclick="setConfirmInvoice4()">Hướng dẫn xử lý</a>' +
              '</div>' +
              '</div>' +
              '</div>' +
              '<div style="width: 50%; display: inline-block; float:rigth;">' +
              '<div style="float: right">' +
              // '<button type="button" class="button-content-style">' +
              // '<a href="javascript:void(0)" class="text-button">Sao chép</a>' +
              // '</button>' +
              // '<button type="button" class="button-content-style">' +
              // '<a href="javascript:void(0)" class="text-button">In chuyển đổi</a>' +
              // '</button>' +
              // '<button type="button" class="button-content-style">' +
              // '<a href="javascript:void(0)" class="text-button">Gửi hóa đơn</a>' +
              // '</button>' +
              '</div>' +
              '</div>' +
              '</div>';
            const myPara1 = '<div class="design-button">' +
              '<div style="width: 50%; display: inline-block; float: left">' +
              '<div class="dropdown">' +
              '<button type="button" class="dropbtn button-content-style">' +
              '<a href="javascript:void(0)" class="text-button">Xử lý hóa đơn</a>' +
              '</button>' +
              '<div class="dropdown-content" style="left:0;">' +
              '<a href="javascript:void(0)" onclick="setConfirmInvoice2()">Thay thế hóa đơn</a>' +
              '<a href="javascript:void(0)" onclick="setConfirmInvoice3()">Ðiều chỉnh hóa đơn</a>' +
              '<a href="javascript:void(0)" onclick="setConfirmInvoice4()">Hướng dẫn xử lý</a>' +
              '</div>' +
              '</div>' +
              '</div>' +
              '<div style="width: 50%; display: inline-block; float:right;">' +
              '<div style="float: right">' +
              // '<button type="button" class="button-content-style">' +
              // '<a href="javascript:void(0)" class="text-button">Sao chép</a>' +
              // '</button>' +
              // '<button type="button" class="button-content-style">' +
              // '<a href="javascript:void(0)" class="text-button">In chuyển đổi</a>' +
              // '</button>' +
              // '<button type="button" class="button-content-style">' +
              // '<a href="javascript:void(0)" class="text-button">Gửi hóa đơn</a>' +
              // '</button>' +
              '</div>' +
              '</div>' +
              '</div>';
            this.checkSubmit.document.write(`
                  <style type="text/css" >
                        .text-button{
                            color: #fff;
                            text-decoration: none;
                        }
                       .design-button{
                            position: fixed;
                            bottom: 0;
                            left: 0;
                            right: 0;
                            padding: 9px 25px;
                            background: white;
                       }
                       .button-content-style{
                          background-color: #337ab7;
                          color: #fff;
                          border: 1px solid #2d6da3;
                          margin: 0 10px;
                          padding: 5px 10px 5px;
                       }
                       .dropdown {
                        position: relative;
                        display: inline-block;
                       }
                       .dropdown-content {
                          display: none;
                          position: absolute;
                          bottom: 28px;
                          background-color: #f9f9f9;
                          min-width: 160px;
                          box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
                          z-index: 1;
                        }
                       .dropdown-content a {
                          color: black;
                          padding: 12px 16px;
                          text-decoration: none;
                          display: block;
                       }
                       .dropdown-content a:hover {background-color: #b5acac;}
                       .dropdown:hover .dropdown-content {
                          display: block;
                        }
                        .dropdown:hover .dropbtn {
                          background-color: #2d6da3;
                        }
                  </style>
               <div>
                    <iframe width="100%" height="94%" style="padding-bottom: 29px" src="${objectUrl}"></iframe>
               </div>
                <script type="text/javascript">
                  function setConfirmInvoice() {
                    document.cookie = "AK_SS_I=CHECKED; path=/";
                    window.close();
                  }
                  function setConfirmInvoice2() {
                    document.cookie = "AK_SS_I=CHECKED2; path=/";
                    window.close();
                  }
                  function setConfirmInvoice3() {
                    document.cookie = "AK_SS_I=CHECKED3; path=/";
                    window.close();
                  }
                  function setConfirmInvoice4() {
                    document.cookie = "AK_SS_I=CHECKED4; path=/";
                    window.close();
                  }
                  function closeWin(){
                    window.close();
                  }
                </script>
            `);
            if (ids.length === 1 || data.status === 'DA_XUAT') {
              if (data.adjustment_type !== 4) {
                this.checkSubmit.document.write(myPara);
              } else {
                this.checkSubmit.document.write(myPara1);
              }
            }
            this.checkSubmit.resizeTo(screen.availWidth, screen.availHeight);
            this.checkSubmit.focus();
            // win.resizeTo(screen.availWidth, screen.availHeight);
            // win.focus();
            // win.open(objectUrl, "name", "width=" + w + ", height=" + h);
          } else {
            this.checkSubmit.close();
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }

    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
}
