import { Injectable } from '@angular/core';

import { ApiService, AuthService } from '@core/index';
import * as moment from 'moment';

@Injectable()
export class ApproveService {

  currentDate = moment(new Date()).format("YYYY-MM-DD");

  constructor(
    private apiService: ApiService,
    private authService: AuthService,
  ) { }

  getAll(params?){
    let company_type = this.authService.getCurrentUser().account.company_type;
    let filter_value;
    switch (company_type) {
      case 'KHAU_TRU':
        filter_value = '01GTKT';
        break;
      case 'TRUC_TIEP':
        filter_value = '02GTTT';
        break;
      default:
        break;
    }
    let includes = 'includes[]=template';
    let filter_string = "&filter_groups[0][filters][0][key]=crm_id&filter_groups[0][filters][0][value]=null&filter_groups[0][filters][0][operator]=eq&filter_groups[0][filters][1][key]=status&filter_groups[0][filters][1][value]=GHI_TAM&filter_groups[0][filters][1][operator]=eq&filter_groups[0][filters][2][key]=invoice_number&filter_groups[0][filters][2][value]=null&filter_groups[0][filters][2][operator]=eq&filter_groups[0][filters][3][key]=adjustment_type&filter_groups[0][filters][3][value]=1&filter_groups[0][filters][3][operator]=eq"

    let filter = filter_value ? filter_string + `&filter_groups[2][filters][0][key]=template.invoice_type&filter_groups[2][filters][0][value]=${filter_value}&filter_groups[2][filters][0][operator]=eq` : filter_string;

    if(params){
      filter += '&' + params;
    }
    let sort = '//&sort[1][key]=created_at&sort[1][direction]=DESC' //&sort[1][key]=invoice_number&sort[1][direction]=DESC
    return this.apiService.getDataTable("/invoices",  includes + filter, sort);
  }

  show(id){
    return this.apiService.get("/invoices/" + id);
  }

  delete(id){
    return this.apiService.delete("/invoices/" + id);
  }
}
